import React, { useState } from 'react'
import { Button, Container, Form, FormControl, Nav, Navbar, NavDropdown, Row } from 'react-bootstrap'

const NavBar = ({filterbySearch}) => {

    // 
    const [searchValue , setSearchValue]= useState('')
    const onSearch=(e)=>{
        //       
        e.preventDefault()
        filterbySearch(searchValue)
        setSearchValue('')
    }

    return (
    <Row>
            <Navbar bg='dark' variant='dark' expand="lg" className="bg-body-tertiary">
    <Container >
        <Navbar.Brand href="#">
            <div className='brand-color'>   </div>
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
        <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
        >
        </Nav>
        <Form className="d-flex">
            <FormControl
            type="text"
            placeholder="  .."
            className="mx-2"
            onChange={(e) => setSearchValue(e.target.value)}
            value={searchValue}
            />
            <button onClick={onSearch} className=' btn-search'></button>
        </Form>
        </Navbar.Collapse>
    </Container>
    </Navbar>
    </Row>
    )
}

export default NavBar
